$BackgroundMain: #000000;
$TextMain: #FFFFFF;


body{
  font-size: 16px;
  background-color: $BackgroundMain;
  background-image: url('../images/bgmain.jpg');
  background-size: cover;
  .bg{
    background-color: #FCFCFD;
    color: $BackgroundMain;
  }
  .bgTitle{
    background-color: transparentize(darken($TextMain, 100%), .4);
    color: $TextMain;
  }
}

h2{
  padding: 5px;
  margin: 0;
  text-transform: uppercase;
}

form{
  padding: 2rem;
}
header{
  #headerTop{
    margin-top: 2rem;
    padding: 1rem;
    #logo{
      height: 80px;
    }
    .navbar-nav{
      margin-top: 15px;
      float: right;
      @media (max-width: 992px) {
        margin-top: 0;
        float: left;
      }
    }
  }

}

main{
  .width50{
    @media (max-width: 768px) {
      width: 48% !important;
    }
  }
  .width100{
    @media (max-width: 768px) {
      width: 100% !important;
    }
  }
}

footer{
  .row{
    background-color: rgba(0, 0, 0, 0.77);
    line-height: 7rem;
    color: #fff;
    font-size: 1.5rem;
  }
}



@media (max-width: 768px) {
  .navbar-nav{
    width:100% !important;
  }
  .navbar-toggle{
    float: none !important;
    width: 100%;
    height: 100%;
    background-image: url('../images/menu.png');
    display: inline-block;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: #FD411A;

  }
  .btnmenu{
    color: #fff;
    font-size: 2rem;
  }
}